import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {CustomerAuthorComponent} from './components/customer-author/customer-author.component';

const routes: Routes = [{path: ':authorId', component: CustomerAuthorComponent}];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule]
})
export class CustomerAuthorRoutingModule {}
