import {Component, OnInit} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {takeUntil} from 'rxjs/internal/operators/takeUntil';
import {AppErrorHandler} from 'src/app/core/error-handler/app-error-handler.service';
import {AuthorResponseVM} from 'src/app/core/models/customer/author/authorResponse.ViewModel';
import {NotificationService} from 'src/app/core/notification/notifications/customer/notification.service';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {AuthorService} from 'src/app/core/services/customer/author/author.service';

@Component({
	selector: 'app-customer-author',
	templateUrl: './customer-author.component.html',
	styleUrls: ['./customer-author.component.less'],
	providers: [DestroyService]
})
export class CustomerAuthorComponent implements OnInit {
	isLoading = true; //skelton
	isNotHaveData: boolean = false;
	authorId: number = 1;
	authorResponse: AuthorResponseVM = {} as AuthorResponseVM;
	constructor(
		private authorService: AuthorService,
		private route: ActivatedRoute,
		private notificationService: NotificationService,
		private destroy$: DestroyService,
		private appErrorHandler: AppErrorHandler
	) {}

	ngOnInit(): void {
		this.isLoading = true;
		if (this.route.snapshot.paramMap.get('authorId')) {
			this.authorId = +this.route.snapshot.paramMap.get('authorId');
			console.log(this.authorId);
			this.getAuthor(this.authorId);
		}
	}
	getAuthor(authorId: number) {
		this.authorService
			.getAuthor(authorId)
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					console.log(result);
					if (result.isSuccessed) {
						if (result.resultObj) {
							console.log(result);
							this.authorResponse = result.resultObj;
							this.isLoading = false;
						}
					} else {
						this.isNotHaveData = true;
						if (result.message) {
							this.notificationService.showErrorNotification(result.message);
						}
					}
				},
				(error) => {
					this.isNotHaveData = true;
					this.appErrorHandler.handleErrorWithNoti(error);
				},
				() => {}
			);
	}
}
