import {Observable} from 'rxjs';
import {ApiResult} from './../../../models/common/ApiResult';
import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {CustomerUrlAuthor} from 'src/app/core/utils/URL/customer-url/customer-author/customer-url-author';
import {AuthorResponseVM} from 'src/app/core/models/customer/author/authorResponse.ViewModel';

@Injectable({
	providedIn: 'root'
})
export class AuthorService {
	constructor(private http: HttpClient, private customerUrlAuthor: CustomerUrlAuthor) {}
	getAuthor(authorId: number): Observable<ApiResult<AuthorResponseVM>> {
		return this.http.get<ApiResult<AuthorResponseVM>>(this.customerUrlAuthor.getAuthor(authorId));
	}
}
